<template>
	<v-card class="ma-md-6 ma-sm-3">
		<v-card-title class="py-2">
			<v-icon class="mr-3">mdi-file-outline</v-icon>
			Tambah Newsletter
		</v-card-title>
		<v-divider></v-divider>
		<v-form ref="form" v-model="valid">
			<v-card-text>		
				<div class="title">Judul</div>	
				<v-text-field
					outline
					outlined
					dense
					v-model="dataBerita.judul"
					:rules="requiredRule"
				></v-text-field>	
				<div class="title">Kategori</div>	
				<v-select
					outline
					outlined
					dense
					:rules="requiredRule"
					:items="dataKategori"
					item-text="nama"					
					item-value="id"	
					v-model="dataBerita.kategori_id"				
				></v-select>
				<div class="title">Artikel</div>	
				<ckeditor :editor="editor" v-model="dataBerita.konten" :config="editorConfig"></ckeditor>
        <br>
        <Notif :msg="msg" :status="status" @visible="visible" />          
			</v-card-text>
			<v-divider></v-divider>
			<v-card-actions>
				<v-spacer></v-spacer>
				<v-btn
					depressed
					type="submit"
					:disabled="!valid"
					:loading="isLoading"
					@click.prevent="validate"
					color="success">
					publish
				</v-btn>
			</v-card-actions>
		</v-form>
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'
	import CKEditor from '@ckeditor/ckeditor5-vue';
	import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

	export default{
		components: {
			ckeditor: CKEditor.component,
			Notif
		},

		data: () => ({
			editor: ClassicEditor,
      editorConfig: {},
			dataBerita: {
				judul: '',
				kategori_id: '',
				konten: ''
			},
			valid: true,
			requiredRule: [v => !!v || 'Data harus di isi'],
			msg: {
        success: 'Data berhasil ditambahkan',
        error: 'Data gagal ditambahkan',
        visible: false
      }
		}),
		
    created() {
      this.$store.dispatch('dataKategori/getDataKategori')
      this.$store.dispatch('dataKategori/updateIsLoading', true)
    },

    computed: { 
    	...mapState('dataBerita', ['status', 'isLoading']), 
    	...mapState('dataKategori', ['dataKategori']) 
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.$store.dispatch('dataBerita/postDataBerita', this.dataBerita)
          this.$store.dispatch('dataBerita/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      }
    }
	}
</script>